.contact_content {
    padding: 20px;
  }
  .contact_content_header-text {
    color: var(--yellow-theme-sub-text-color);
    display: inline-block;
    font-size: 22px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: 0.5px;
    position: relative;
    text-transform: uppercase;
    padding: 5px 10px;
    border-bottom: 2px solid var(--yellow-theme-main-color);
    &_header-text::before,
    &_header-text::after {
      position: absolute;
      content: "";
      width: 2px;
      height: 5px;
      background-color: var(--yellow-theme-main-color);
      bottom: -1px;
    }
    &_header-text::before {
      left: 0;
    }
    &_header-text::after {
      right: 0;
    }
  }
  .contact_content_form {
    margin-top: 30px;
    button {
      cursor: pointer;
      padding: 10px 70px;
      font-size: 2rem;
      background: transparent;
      color: var(--yellow-theme-main-color);
      border: 1.5px solid var(--yellow-theme-main-color);
      border-radius: 4px;
      font-weight: 500;
      &:hover {
        color: #000;
        background: var(--yellow-theme-main-color);
      }
    }
  }
  .contact_content_form_controlwrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    margin-bottom: 30px;
  
    dev {
      position: relative;
    }
    .inputname,
    .inputemail,
    .inputdescription {
      width: 100%;
      border: 2px solid var(--yellow-theme-main-color);
      padding: 16px 12px;
      transition: all 0.5s ease;
      position: relative;
      background-color: transparent;
      color: var(--yellow-theme-sub-text-color);
    }
    .namelabel,
    .emaillabel
     {
      font-weight: 1000;
      position: relative;
      top: -79px;
      left: 12px;
      transform: all 0.2s ease;
      color: var(--yellow-theme-sub-text-color);
      font-size: 20px;
    }
    .descriptionlabel {
        font-weight: 1000;
        position: relative;
        top: -140px;
        left: 12px;
        transform: all 0.2s ease;
        color: var(--yellow-theme-sub-text-color);
        font-size: 20px;
      }
    
  }
  @media only screen and (max-width: 768px) {
    .contact_content_form {
      text-align: center;
    }
    .contact_content_form_controlwrapper {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
